@import "./assets/styles/entry.scss";

.wrap {
  margin-top: 80px;
  margin-bottom: 80px;
  @include wrap();
  @include max-vw($hd) {
    margin-top: 60px;
    margin-bottom: 60px;
  }
  @include max-vw($lg) {
    margin-top: 30px;
    margin-bottom: 40px;
  }
  @media print {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.dark {
  background-color: red;
}
